import Vue  from 'vue-next/dist/vue';
import d3 from 'd3';

var demo = new Vue({
  el: '#demo',
  template:`
  <svg v-bind:width="swidth"  v-bind:height="sheight"
>
<g v-bind:transform="gtransform" >
  <path class="line" v-bind:d="linedata" ></path>
    <circle  v-for="item in filterStyle" v-bind:r="item.r" v-bind:cx="item.cx" v-bind:cy="item.cy" v-bind:fill="item.fill"></circle>
  <g class="x axis"   v-bind:transform="xtransform" >
    <g class="tick" v-for="item in xScale" v-bind:transform="item.xdata"  style="opacity: 1">
      <line :x2="linestyle.x2" :y2="linestyle.y2"/>
      <text :dy="tstyle.dy" :y="tstyle.y" :x="tstyle.x" style="text-anchor: middle;">{{item.xd}}</text>
    </g>
    <path class="domain" d="M0,6V0H530V6"></path>
  </g>
  <g class="y axis">
    <g class="tick" v-for="item in yScale" v-bind:transform="item.ydata" style="opacity: 1;" >
      <line x2="-6" y2="0"></line>
      <text dy=".32em" x="-9" y="0" style="text-anchor: end;">{{item.yd}}</text>
    </g>
    <path class="domain" d="M-6,0H0V210H-6"></path>
  </g>
</g>

</svg>
  `,
  data () {
  const margin = {top: 30, right: 20, bottom: 30, left: 50}
  const parseDate = d3.time.format('%d-%b-%y').parse
  const height = 270 - margin.top - margin.bottom
  const width = 600 - margin.left - margin.right
  const _height = height + margin.top + margin.bottom
  const _width = width + margin.left + margin.right
  const x = d3.time.scale().range([0, width])
  const y = d3.scale.linear().range([height, 0])
  const valueline = d3.svg.line()
  .x(d => { return x(d.date) })
  .y(d => { return y(d.close) })
  return {
    x: x,
    y: y,
    linestyle: {
      y2: '6',
      x2: '0'
    },
    parseDate: parseDate,
    valueline: valueline,
    swidth: _width,
    sheight: _height,
    height: height,
    width: width,
    gtransform: 'translate(' + margin.left + ',' + margin.top + ')',
    linedata: '',
    xtransform: 'translate(' + 0 + ',' + height + ')',
    xScale: [],
    yScale: [],
    filterStyle: null,
    tstyle: {
      dy: '.71em',
      y: '9',
      x: 0,
      textachor: 'middle'
    }
  }
},
created () {
  this.fetchData()
},
methods: {
  fetchData () {
    d3.csv('https://raw.githubusercontent.com/lincolnphu/React-W3school-Example.github.io/master/data.csv', data => {
      const {x, width, height, y, parseDate, valueline} = this
      data.forEach(d => {
        d.date = parseDate(d.date)
        d.close = +d.close
      })
      x.domain(d3.extent(data, d => { return d.date }))
      y.domain([0, d3.max(data, d => { return d.close })])
      this.linedata = valueline(data)
      this.xScale = x.ticks(5).map(x.tickFormat(5, '')).map((d, n) => {
        return {
          xdata: 'translate(' + (6 + n * 7) * (width / 36) + ',' + 0 + ')',
          xd: d
        }
      })
      this.yScale = y.ticks(6).map((d, i) => {
        return {
          ydata: 'translate(' + 0 + ',' + ((height / 636.36) * (636.36 - d)) + ')',
          yd: d
        }
      })
      this.filterStyle = data.map(d => {
        if (d.close < 400) {
          return {
            fill: 'red',
            r: '3.5',
            cx: x(d.date),
            cy: y(d.close)
          }
        } else {
          return {
            fill: '',
            r: '',
            cx: '',
            cy: ''
          }
        }
      })
    })
  }
},
})



